<template lang="">
    <div class="w-full">
      <div class="max-w-5xl mx-auto flex flex-wrap px-[2%]">
        <div class="py-24 pt-45 text-center basis-full">
          <h3 class="text-3xl font-bold mb-5"></h3>
          <h2 class="text-xl font-bold mb-5"></h2>
        </div>
        <div class="basis-full pb-24">
          <div class="sm:max-w-md mx-auto py-4">
            <div class="basis-full px-10 text-center">
              <div class="mt-3 pb-10">
                <div class="flex justify-between items-center border-b mb-5">
                  <label class="mr-2 w-20 text-left"></label>
                  <input v-model="email" type="email" placeholder=" " class="focus:outline-opacity-60 text-left p-2 w-full">
                </div>
                <div class="flex justify-between items-center border-b mb-5">
                  <label class="mr-2 w-20 text-left"></label>
                  <input v-model="password" type="password" placeholder=" " class="text-left p-2 w-full">
                </div>
                <div class="flex justify-between items-center border-b mb-5">
                  <label class="mr-2 w-20 text-left"></label>
                  <input type="password" v-model="password2" placeholder=" " class="text-left p-2 w-full">
                </div>
                <div class="flex justify-between items-center border-b mb-5">
                  <label class="mr-2 w-20 text-left"></label>
                  <input v-model="nickname" type="text" placeholder=" " class="text-left p-2 w-full">
                </div>
              </div>
              <button @click="signUp" class="border my-5 w-full text-white/90 bg-slate-300 hover:bg-slate-500  rounded-lg text-center py-2" type="submit">
                  
              </button>
            </div>
          </div>
        </div>
      </div>

    </div>
  </template>
  <script>
  import { auth } from '@/firebase';

  export default {
      name:"SignupPage",
      data() {
          return {
              email:"",
              password:"",
              password2:"",
              nickname:""
          }
      },
      methods:{
        signUp(){

          if(this.password != this.password2){
            alert(' .  ')
            return
          } else {
            auth.createUserWithEmailAndPassword(this.email,this.password).then((result)=>{
              result.user.updateProfile({displayName:this.nickname})
            })
            this.$router.replace('/')
          }

        },
    }

  }
  </script>
  <style>

  </style>